export default function _searchTags(word, array, field ) {
  let tmpArray = [];

  //si la variable campo estÃ¡ vacÃ­a se agrega "tags" por defecto
  if (!field) {
    field = "tags"
  }

  word = word.toLowerCase();
  word = eliminarTildes(word);
  //console.log("word ingresada:", word);
  const patt = new RegExp(word);  

  array.forEach((item) => {
    let str =  eliminarTildes (item[field]);
    str = str.toLowerCase();
    const res = patt.test(str);
    if (res) {
      tmpArray.push(item);
    }
  }); 
  return tmpArray;
}

const eliminarTildes = (cadena) => {
  //console.log("cadena:",cadena);
  let tmpCadena = cadena.replace(/Ã¡/g, "a");
  tmpCadena = tmpCadena.replace(/Ã©/g, "e");
  tmpCadena = tmpCadena.replace(/Ã­/g, "i");
  tmpCadena = tmpCadena.replace(/Ã³/g, "o");
  tmpCadena = tmpCadena.replace(/Ãº/g, "u");
  //console.log("tmpCadena",tmpCadena);
  return tmpCadena;
};
